.crypto-nft-con{
    max-width: 900px;
    margin: 0 auto;
    border-radius: 8px ;
    border: 1px solid rgba(255, 255, 255, 0.05);
}
.crypto-nft-btn-con{
    border-radius: 8px 8px 0 0 ;
}
.crypto-nft-btn-con button{
    background-color: transparent;
    border: 0;
    border-bottom: 2px solid transparent;
    color: white;
    margin-right: 16px;
    padding: 16px 32px;
    cursor: pointer;
    font-weight: bold;
}
.crypto-nft-con{
    background-color: rgba(255, 255, 255, 0.01);
    padding: 16px;
}