footer {
    background-color: rgba(255, 255, 255, 0.02);
    border-top: 1px solid rgba(255, 255, 255, 0.05);
    color: #fff;
    padding: 20px 0;
    
  }
  
  .footer-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 48px 0;
  }
  .footer-container h2{
    /* font-size: 3rem; */
    margin-bottom: 16px;
  }
  .footer-container p{
    color: rgba(255, 255, 255, 0.5);
  }
  .footer-column {
    flex: 0 0 30%;
    max-width: 30%;
    padding-right: 30px;
  }
  
  .footer-column h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .footer-column p,
  .footer-column ul {
    font-size: 14px;
    line-height: 24px;
  }
  
  .footer-column ul {
    padding-left: 0;
    list-style-type: none;
  }
  
  .footer-column ul li {
    margin-bottom: 8px;
  }
  
  .footer-column ul li a {
    color: #fff;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.5);
    transition: 0.3s ease-in-out;
  }
  .footer-column ul li a:hover{
    color: goldenrod;
  }
  
  .social-icons li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .social-icons li a {
    color: #fff;
    font-size: 20px;
  }
  
  @media screen and (max-width: 768px) {
    .footer-column {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 20px;
      padding-right: 0;
      /* text-align: center/; */
      padding: 16px 32px;
    }
  }
  