.banner-phn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    /* background-color: rgb(195, 127, 9); */
    padding-top: 3%;
    padding-bottom: 3%;
    background-color: rgba(255, 255, 255, 0.01);
    border: 1px solid rgba(255, 255, 255, 0.05);
}
.banner-phn .item{
    text-align: center;
    padding: 32px 16px;
    border-radius: 8px;
}
.banner-phn .item img{
    width: 70px;
    margin-bottom: 16px;
}
.banner-phn .item h2{
    margin-bottom: 16px;
    color: goldenrod;
}
/* .banner-phn .item p{} */
@media ( max-width : 450px ){
    .banner-phn{
        flex-direction: column;
        padding: 48px 24px;
    }
}