.banner{
    background-color: rgb(234, 169, 57);
    color: rgb(22, 22, 31);
}
.banner .banner-flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5% 0;
}
.banner .banner-flex .left{
    width: 70%;
}
.banner .banner-flex .left h2{
    font-size: 3rem;
}
.banner .banner-flex .right{
}
.banner .banner-flex .right button{
    background-color: rgb(20, 20, 32);
    color: white;
    padding: 16px 32px;
    border: 2px solid transparent;
    border-radius: 4px;
    font-weight: bold;
}
.banner .banner-flex .right button:hover{
    background-color: transparent;
    border: 2px solid black;
    color: black;
}
@media (max-width:600px){
    .banner .banner-flex{
        flex-direction: column;
        gap: 16px;
        align-items: flex-start;
        padding: 96px 32px ;
    }
    .banner .banner-flex .left{
        width: 100%;
    }
}