.App {
}

.custom-container{
  padding: 0 8%;
}
@media (max-width:450px){
  .custom-container{
    padding: 0 16px;
  }
}
a{
  text-decoration: none;
  color: #adb5bd;
}

section{}
section .section-title{}
section .section-title h1{}
.panel{
  /* background-color: red; */
  height: 20px;
  margin-top: 80px;
}

table{
  /* background-color: red; */
  /* border: 1px solid white; */
  width: 100%;
  border-collapse: collapse;
}
table tr{
  padding: 10px;
}
td{
  padding: 16px;
}
table tr td{
  padding-bottom: 16px;
  padding-top: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}
thead{
  color: rgb(197, 141, 0);
  color: rgb(255, 255, 255);
  /* font-weight: bold; */
  box-shadow: 0 20px 25px 2px rgba(165, 80, 255, 0.02);
}
thead th{
  padding: 16px;
  padding-bottom: 20px;
  padding-top: 18px;
  border: 1px solid rgba(255, 255, 255, 0.01);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  text-align: left;
}

@media (max-width: 500px){
  .hide-in-phn{
      display: none !important;
  }
}