.hero{
    height: calc(100vh - 70px);
    width: 100%;
    content: '';

    display: flex;
    align-items: center;
    justify-content: space-between;
}
.hero .left{
    width: 40%;
}
.hero .left h1{
    color: white;
    margin-bottom: 16px;
    font-size: 3rem;
}
.hero .left p{
    color: rgba(255, 255, 255, 0.4);
}

.hero .left p:nth-child(2){
    font-size: 1.25rem;
    margin-bottom: 16px;
}
.hero .sign-up-btn{
    padding: 12px 24px !important;
    width: fit-content;
    transition: 0.3s ease-in-out;
    font-weight: bold;
    margin-top: 48px !important;
    box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
}
.hero .right{
    width: 50%;
}
.hero .right img{
    width: 100%;
    min-height: 100px;
}
@media (min-width:1000px){
    .hero .left h1{
        color: white;
        margin-bottom: 16px;
        font-size: 5rem;
    }
    .hero .left p{
        color: rgba(255, 255, 255, 0.4);
    }
}
@media (max-width:600px){
    .hero{
        flex-direction: column-reverse;
        justify-content: flex-end;
        height: auto;
        text-align: center;
    }
    .hero .left, 
    .hero .right{
        width: 100%;
    }
    .hero .sign-up-btn{
        /* width: fit-content; */
        margin-top: 24px !important;
        width: 100% !important;
        box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
    }
}