.banner-smart-phone{
    /* background-color: red; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.banner-smart-phone .left{
    flex: 1;
}
.banner-smart-phone .left h2{
    font-size: 5rem;
    line-height: 70px;
    margin-bottom: -32px;
    color: rgba(255, 255, 255, 0.02);
}
.banner-smart-phone .left h1{
    font-size: 3rem;
    margin-bottom: 16px;
    color: white;
}
.banner-smart-phone .left a{
    margin-bottom: 16px;
    display: block;
    color: black;
    background-color: goldenrod;
    width: fit-content;
    font-weight: bold;
    padding: 16px 32px;
    border-radius: 4px;
    box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
    margin-top: 32px;
    margin-bottom: 48px;
    border: 1px solid transparent;
    transition: 0.3s ease-in-out;
}
.banner-smart-phone .left a:hover{
    background-color: transparent;
    border: 1px solid goldenrod;
    color: goldenrod;
}
.banner-smart-phone .left p{
    
}
.banner-smart-phone .right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.banner-smart-phone .right img{
    width: 100%;
}
@media (min-width:900px) {
    .banner-smart-phone .right img{
        width: 60%;
    }
}
@media (max-width:900px){
    .banner-smart-phone{
        flex-direction: column-reverse;
        text-align: center;
    }
    .banner-smart-phone .left a{
        width: 100%;
    }
    .banner-smart-phone .right img{
        width: 80%;
    }
}