.coin-table-container{
    /* min-width: ; */
    overflow: auto;
    position: relative;
    min-height: 600px;
}
.coins{
    
}
.coins .item{
    /* padding:8px 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.03);
    display: flex;
    width: 100%; */
}
.coins .item:hover{
    background-color: rgba(255, 255, 255, 0.05) !important;
}
.coins .item img{
    width: 30px;
}

.coins .item:nth-child(2n-1){
    background-color: rgba(255, 255, 255, 0.01);
}
.pagination{
    margin-top: 16px;
    display: flex;
    justify-content: center;
}
.coin-info{
    display: flex;
    width: fit-content;
    align-items: center;
    font-size: 0.7rem;
}
.pagination div{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-right: 8px;
    margin-left: 8px;
    border-radius: 4px;
    transition: 0.2s ease-in-out;
}
.pagination div:hover{
    background-color: rgba(218, 165, 32, 0.479);
}
.pagination div.active{
    background-color: goldenrod;
    color: black;
}
.pagination div img{
    width: 12px;
    opacity: 0.7;
}
.pagination div:nth-child(1){
    transform: rotateZ(180deg);
}



/* loader  */
.coin-table-container .loader-con{
    position: absolute;
    width: 100%;
    height: 100%;
    content: '';
    background-color: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(5px);
    display: flex;
    align-items: center;
    justify-content: center;
}
.loader {
    border: 6px solid #f3f3f3;
    border-radius: 50%;
    border-top: 6px solid goldenrod;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 11s linear infinite; /* Safari */
    animation: spin 1s linear infinite;
  }
  
  /* Safari */
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  