header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 16px !important;
    padding-bottom: 16px !important;
    border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}
header .left{}
header .right{}
header .right ul{
    list-style-type: none;
}
header .right ul li{
    display: inline;
    margin-left: 16px;
    padding: 10px 14px ;
    font-weight: 500;
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

header .right ul li.active{
    color: goldenrod;
}
header .right ul li:hover{
    color: rgba(218, 165, 32, 0.6);
}
.sign-up-btn{
    background-color: rgb(210, 150, 0);
    color: black !important;
    border-radius: 8px;
    border: 1px solid transparent;
    box-shadow: 0 8px 32px 4px rgba(218, 165, 32, 0.2) !important;
}
.sign-up-btn:hover{
    background-color: transparent;
    color: goldenrod !important;
    border: 1px solid goldenrod;
    box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
}
.hamburger{
    /* background-color: red; */
    width: fit-content;
    display: none;
    cursor: pointer;
}
.hamburger img{
    width: 30px;
    transform: rotateZ(180deg);
}
.close-icon{
    display: none;
    cursor: pointer;
}
.close-icon img{
    width: 30px;
}
nav{
    padding: 8px;
}
@media (max-width:900px){
    .hamburger{
        display: block;
    }
    nav{
        position: fixed;
        height: 100%;
        width: 100%;
        top: 0;
        right: 0;
        background-color: rgba(19, 21, 30, 0.793);
        backdrop-filter: blur(10px);
        transition: .5s ease-in-out;
        padding: 0;
        z-index: 1000;
    }
    nav ul {
        display: flex;
        flex-direction: column;
        padding: 0 32px;
    }
    nav ul li{
        padding: 8px 16px  !important;
        display: block !important;
        margin-bottom: 8px;
        margin-left: 0 !important;
    }
    .close-icon{
        display: block;
        padding: 16px;
        background-color: rgba(255, 255, 255, 0.01);
        text-align: right;
        margin-bottom: 16px;
    }
    .sign-up-btn{
        padding-bottom: 12px !important;
        margin-top: 16px !important;
        box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
    }
}