.nft{
    padding: 16px 0;
    position: relative;
}
.nft .locker{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    background-color: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
}
.nft img{
    width: 30px;
}
.locker button{
    padding: 12px 24px !important;
    width: fit-content;
    transition: 0.3s ease-in-out;
    font-weight: bold;
    margin-top: 48px !important;
    box-shadow: 0 12px 32px 12px rgba(218, 165, 32, 0.2) !important;
    background-color: goldenrod;
    border: none;
    border-radius: 4px;
    display: flex;
    align-items: center;
    font-weight: 700;
}
.locker button:hover{
    background-color: transparent;
    border: 1px solid goldenrod;
    color: goldenrod;
    cursor: pointer;
}

.locker i{
    margin-right: 8px;
}